@media (max-width: 767.98px) {
    body {
        #root {
            .nav {
                height: 5rem;
                .Link {
                    display: none;
                }
                .Toggle {
                    display: block;
                    width: 1rem;
                    height: 1rem;
                    margin-right: 2rem;
                    cursor: pointer;
                }
            }
        }
    }
}

